___CSS_LOADER_EXPORT___.push([module.id, ".btn-travel[data-v-b4ef859a]{background:#e17e4e;background-color:#e17e4e;background-image:none;background-image:linear-gradient(to bottom left,#e17e4e,#ffbf00,#e17e4e);background-position:100% 0;background-position-x:0;background-position-y:0;background-size:auto;background-size:210% 210%;border:none;box-shadow:none;color:#fff;max-width:300px;transition:all .15s ease}", "",{"version":3,"sources":["webpack://./resources/js/desktop/pages/admin/Branches/Index.vue"],"names":[],"mappings":"AA2SA,6BACA,kBAAA,CASA,wBAAA,CALA,qBAAA,CAEA,wEAAA,CAEA,0BAAA,CANA,uBAAA,CACA,uBAAA,CAEA,oBAAA,CAEA,yBAAA,CAOA,WAAA,CAHA,eAAA,CACA,UAAA,CACA,eAAA,CAHA,wBAKA","sourcesContent":["<template>\n    <b-container fluid>\n        <div class=\"row w-100 my-3 mx-auto\">\n            <div class=\"col-12 col-md-4 col-lg-6\">\n                <create></create>\n            </div>\n        </div>\n        <b-row>\n            <b-col lg=\"6\" class=\"my-1\">\n                <b-form-group\n                    label=\"Сортировка\"\n                    label-cols-sm=\"3\"\n                    label-align-sm=\"right\"\n                    label-size=\"sm\"\n                    label-for=\"order_bySelect1\"\n                    class=\"mb-0\"\n                >\n                    <b-input-group size=\"sm\">\n                        <b-form-select v-model=\"order_by\" id=\"order_bySelect1\" :options=\"sortOptions\" class=\"w-75\">\n                            <template v-slot:first>\n                                <option value=\"\">-- none --</option>\n                            </template>\n                        </b-form-select>\n                        <b-form-select v-model=\"sortDesc\" size=\"sm\" :disabled=\"!order_by\" class=\"w-25\">\n                            <option :value=\"false\">Asc</option>\n                            <option :value=\"true\">Desc</option>\n                        </b-form-select>\n                    </b-input-group>\n                </b-form-group>\n            </b-col>\n\n            <b-col lg=\"6\" class=\"my-1\">\n                <b-form-group\n                    label=\"Поиск\"\n                    label-cols-sm=\"3\"\n                    label-align-sm=\"right\"\n                    label-size=\"sm\"\n                    label-for=\"filterInput\"\n                    class=\"mb-0\"\n                >\n                    <b-input-group size=\"sm\">\n                        <b-form-input\n                            v-model=\"filter\"\n                            type=\"search\"\n                            id=\"filterInput\"\n                            placeholder=\"Введите для поиска\"\n                        />\n                        <b-input-group-append>\n                            <b-button :disabled=\"!filter\" @click=\"filter = ''\">Очистить</b-button>\n                        </b-input-group-append>\n                    </b-input-group>\n                </b-form-group>\n            </b-col>\n\n            <b-col sm=\"5\" md=\"6\" class=\"my-1\">\n                <b-form-group\n                    label=\"На странице\"\n                    label-cols-sm=\"6\"\n                    label-cols-md=\"4\"\n                    label-cols-lg=\"3\"\n                    label-align-sm=\"right\"\n                    label-size=\"sm\"\n                    label-for=\"perPageSelect1\"\n                    class=\"mb-0\"\n                >\n                    <b-form-select\n                        v-model=\"perPage\"\n                        id=\"perPageSelect1\"\n                        size=\"sm\"\n                        :options=\"pageOptions\"\n                    />\n                </b-form-group>\n            </b-col>\n\n            <b-col sm=\"7\" md=\"6\" class=\"my-1\">\n                <b-pagination\n                    v-model=\"currentPage\"\n                    :total-rows=\"totalRows\"\n                    :per-page=\"perPage\"\n                    align=\"fill\"\n                    size=\"sm\"\n                    class=\"my-0\"\n                />\n            </b-col>\n        </b-row>\n        <b-table\n            show-empty\n            small\n            stacked=\"md\"\n            :items=\"branches\"\n            :fields=\"fields\"\n            :current-page=\"currentPage\"\n            :per-page=\"perPage\"\n            :filter=\"filter\"\n            :filterIncludedFields=\"filterOn\"\n            :sort-by.sync=\"order_by\"\n            :sort-desc.sync=\"sortDesc\"\n            :sort-direction=\"order_direction\"\n            @filtered=\"onFiltered\"\n            :busy=\"loading\"\n            empty-text=\"Нет записей для отображения\"\n            empty-filtered-text=\"Нет записей, соответствующих вашему запросу\"\n        >\n            <template v-slot:cell(country)=\"data\">\n                <b-input-group size=\"sm\">\n                    {{data.item.country}}\n<!--                    <b-form-input :value=\"data.item.country\"-->\n<!--                                  @blur=\"save($event.target.value,data.item.id,'country')\"-->\n<!--                                  placeholder=\"Введите \"/>-->\n<!--                    <multiselect-->\n<!--                        v-model=\"data.item.country\"-->\n<!--                        :options=\"countries\"-->\n<!--                        :option-height=\"25\"-->\n<!--                        placeholder=\"Выберете страну\"-->\n<!--                        :show-labels=\"false\"-->\n<!--                        :maxHeight=\"200\"-->\n<!--                        label=\"Name\"-->\n<!--                        track-by=\"Name\"-->\n<!--                        :loading=\"isCountriesLoading\"-->\n<!--                        :disabled=\"isCountriesLoading\"-->\n<!--                        :allow-empty=\"false\"-->\n<!--                        @input=\"save($event.target.value,data.item.id,'country')\"-->\n<!--                    >-->\n<!--                        <template slot=\"singleLabel\" slot-scope=\"props\">-->\n<!--                            <b :class=\"'tc-flag-'+props.option.Id\"></b>-->\n<!--                            <span class=\"option__desc\">-->\n<!--                                <span class=\"option__title\">{{ props.option.Name }}</span>-->\n<!--                            </span>-->\n<!--                        </template>-->\n<!--                        <template slot=\"option\" slot-scope=\"props\">-->\n<!--                            <b :class=\"'tc-flag-'+props.option.Id\"></b>-->\n<!--                            <span class=\"option__desc\">-->\n<!--                                <span class=\"option__title\">{{ props.option.Name }}</span>-->\n<!--                            </span>-->\n<!--                        </template>-->\n<!--                    </multiselect>-->\n                </b-input-group>\n            </template>\n            <template v-slot:cell(city)=\"data\">\n                <b-input-group size=\"sm\">\n                    <b-form-input\n                        v-model=\"data.item.city\"\n                        @blur=\"save($event.target.value, data.item.id,'city')\"\n                        placeholder=\"Введите город\"\n                        class=\"travel-input\"\n                    >\n                    </b-form-input>\n                </b-input-group>\n            </template>\n            <template v-slot:cell(action1)=\"data\">\n                <b-input-group size=\"sm\" >\n                    <b-button  @click=\"seeDepartments(data.item.id)\" class=\"btn btn-info mr-1 mb-1 btn-travel w-100\"\n                              :disabled=\"data.item.id===null\">\n                        Отделения\n                    </b-button>\n                </b-input-group>\n            </template>\n            <template v-slot:cell(action2)=\"data\">\n                <b-input-group size=\"sm\" >\n                    <b-button  @click=\"remove(data.item.id)\" class=\"btn btn-info mr-1 mb-1 btn-travel w-100\"\n                              :disabled=\"data.item.id===null\">\n                        Удалить\n                    </b-button>\n                </b-input-group>\n            </template>\n            <template v-slot:table-busy>\n                <div class=\"text-center text-warning my-2\">\n                    <b-spinner class=\"align-middle\" variant=\"warning\"/>\n                    <strong>Загрузка...</strong>\n                </div>\n            </template>\n        </b-table>\n    </b-container>\n</template>\n\n<script>\n    import Create from \"./Create\";\n    export default {\n        name: \"Index\",\n        components: {\n            Create\n        },\n        data() {\n            return {\n                order_by: 'id',\n                sortDesc: false,\n                totalRows: 1,\n                currentPage: 1,\n                perPage: 5,\n                order_direction: 'asc',\n                filter: null,\n                filterOn: [],\n\n                pageOptions: [5, 10, 15, 25, 50, 100],\n\n                fields: [\n                    {key: 'id', label: 'ID', sortable: true, order_direction: 'desc'},\n                    {key: 'country', label: 'Страна', sortable: true, order_direction: 'desc'},\n                    {key: 'city', label: 'Город', sortable: true, order_direction: 'desc'},\n                    {key: 'action1', label: 'Показать'},\n                    {key: 'action2', label: 'Действия'}\n                ],\n                 loading:false,\n            }\n        },\n        computed: {\n            sortOptions() {\n                return this.fields\n                    .filter(f => f.sortable)\n                    .map(f => {\n                        return {text: f.label, value: f.key}\n                    })\n            },\n            branches() {\n                return this.$store.getters.branches;\n            },\n            countries() {\n                return this.$store.getters.countries;\n            },\n            isCountriesLoading() {\n                return this.$store.getters.isCountriesLoading;\n            },\n            isCitiesLoading() {\n                return this.$store.getters.isCitiesLoading;\n            },\n        },\n        mounted() {\n            if(this.branches.length<=0){\n                this.loadBranches();\n            }\n        },\n        methods: {\n            seeDepartments(id) {\n                this.$router.push('/admin/branches/'+id)\n            },\n            async loadBranches() {\n                await this.$store.dispatch('setBranches')\n            },\n            sendMessage(message) {\n                this.$notify({\n                    group: 'info',\n                    type: 'travel',\n                    title: 'Филиалы',\n                    text: message\n                });\n            },async remove(id) {\n                this.$store.dispatch('removeBranch', id).then(resp => {\n                    this.sendMessage(resp.data.message)\n                })\n            },\n\n            async save(value, id, key) {\n                this.$store.dispatch('saveBranch', {\n                    id: id,\n                    key: key,\n                    value: value\n                }).then(resp => {\n                    this.$store.commit('saveBranch', resp.data.branch);\n                    this.sendMessage(resp.data.message)\n                });\n            },\n\n            onFiltered(filteredItems) {\n                this.totalRows = filteredItems.length;\n                this.currentPage = 1\n            },\n        }\n    }\n</script>\n\n<style scoped>\n    .btn-travel{\n        background: #e17e4e;\n        background-color: rgb(225, 126, 78);\n        background-position-x: 0%;\n        background-position-y: 0%;\n        background-image: none;\n        background-size: auto;\n        background-image: linear-gradient(to bottom left, #e17e4e, #ffbf00, #e17e4e);\n        background-size: 210% 210%;\n        background-position: top right;\n        background-color: #e17e4e;\n        transition: all 0.15s ease;\n        box-shadow: none;\n        color: #ffffff;\n        max-width: 300px;\n        border: none;\n    }\n</style>\n<style>\n    .page-item.active .page-link {\n        background: #f08b23;\n        border: 1px solid #f08b23;\n        transition: all 0.15s ease;\n        box-shadow: none;\n        color: #ffffff;\n    }\n</style>\n"],"sourceRoot":""}]);
