___CSS_LOADER_EXPORT___.push([module.id, ".btn-travel[data-v-37298eac]{background:#e17e4e;background-color:#e17e4e;background-image:none;background-image:linear-gradient(to bottom left,#e17e4e,#ffbf00,#e17e4e);background-position:100% 0;background-position-x:0;background-position-y:0;background-size:auto;background-size:210% 210%;border:none;box-shadow:none;color:#fff;max-width:300px;transition:all .15s ease}", "",{"version":3,"sources":["webpack://./resources/js/desktop/pages/admin/Branches/Create.vue"],"names":[],"mappings":"AA4NA,6BACA,kBAAA,CASA,wBAAA,CALA,qBAAA,CAEA,wEAAA,CAEA,0BAAA,CANA,uBAAA,CACA,uBAAA,CAEA,oBAAA,CAEA,yBAAA,CAOA,WAAA,CAHA,eAAA,CACA,UAAA,CACA,eAAA,CAHA,wBAKA","sourcesContent":["<template>\n    <div>\n        <b-modal id=\"new-branch\" scrollable centered title=\"Создать новый филиал\">\n            <div class=\"row m-auto w-100 justify-content-center align-items-start\" style=\"min-height: 210px\">\n                <div class=\"col-12\">\n                    <b-form-group label=\"Страна\" class=\"mb-0\">\n                        <multiselect\n                            v-model=\"new_branch.country\"\n                            :options=\"countries\"\n                            :option-height=\"25\"\n                            placeholder=\"Выберете страну\"\n                            :show-labels=\"false\"\n                            :maxHeight=\"150\"\n                            label=\"Name\"\n                            track-by=\"Name\"\n                            :loading=\"isCountriesLoading\"\n                            :disabled=\"isCountriesLoading\"\n                            :allow-empty=\"false\"\n                        >\n                            <template slot=\"singleLabel\" slot-scope=\"props\">\n                                <b :class=\"'tc-flag-'+props.option.Id\"></b>\n                                <span class=\"option__desc\">\n                                <span class=\"option__title\">{{ props.option.Name }}</span>\n                            </span>\n                            </template>\n                            <template slot=\"option\" slot-scope=\"props\">\n                                <b :class=\"'tc-flag-'+props.option.Id\"></b>\n                                <span class=\"option__desc\">\n                                <span class=\"option__title\">{{ props.option.Name }}</span>\n                            </span>\n                            </template>\n                        </multiselect>\n                    </b-form-group>\n                </div>\n                <div class=\"col-12\">\n                    <b-form-group label=\"Город\" class=\"mb-0\">\n                        <b-form-input\n                            v-model=\"new_branch.city\"\n                            :placeholder=\"'Введите город'\"\n                            class=\"travel-input\"\n                        >\n                        </b-form-input>\n                    </b-form-group>\n                </div>\n            </div>\n\n            <template v-slot:modal-footer>\n                <div class=\"w-100\">\n                    <b-button\n                        variant=\"primary\"\n                        class=\"float-right btn-travel\"\n                        @click=\"createBranch\"\n                        :disabled=\"new_branch.country==='' || new_branch.city===''\"\n                    >\n                        <div class=\"row align-items-center justify-content-center m-auto w-100 h-100 text-center\">\n                            <span v-if=\"loading\" class=\"spinner-border spinner-border-sm mx-2\" role=\"status\" aria-hidden=\"true\"></span>\n                            <span v-if=\"loading\" class=\"sr-only\">Loading...</span>\n                            Сохранить\n                        </div>\n                    </b-button>\n                    <b-button\n                        variant=\"primary\"\n                        class=\"float-right me-2 btn-travel\"\n                        @click=\"cancel\"\n                    >\n                        Отмена\n                    </b-button>\n                </div>\n            </template>\n        </b-modal>\n        <b-button variant=\"primary\" class=\"w-100 btn-travel\" v-b-modal.new-branch>Создать филиал</b-button>\n    </div>\n</template>\n\n<script>\n    export default {\n        data() {\n            return {\n                loading: false,\n\n                new_branch: {\n                    country: '',\n                    country_alias: '',\n                    city:'',\n                },\n                template: null,\n            }\n        },\n        computed:{\n            countries() {\n                return this.$store.getters.countries;\n            },\n            isCountriesLoading() {\n                return this.$store.getters.isCountriesLoading;\n            },\n            isCitiesLoading() {\n                return this.$store.getters.isCitiesLoading;\n            },\n        },\n        created() {\n            this.$store.dispatch('getCountries', {town_id:0});\n        },\n        mounted() {\n            this.template = JSON.parse(JSON.stringify(this.new_branch));\n        },\n        methods:{\n            createBranch(){\n                this.loading=true;\n                this.new_branch.country_alias = this.new_branch.country.Alias;\n                this.new_branch.country = this.new_branch.country.Name;\n\n                this.$store.dispatch('addBranch', this.new_branch).then(resp =>{\n                    this.sendMessage('Филиал успешно сохранён')\n                    this.loading=false;\n                    this.$bvModal.hide(\"new-branch\");\n                });\n            },\n            cancel(){\n                this.$bvModal.hide(\"new-branch\");\n            },\n            sendMessage(message) {\n                this.$notify({\n                    group: 'info',\n                    type: 'travel',\n                    title: 'Филиалы',\n                    text: message\n                });\n            },\n        }\n    }\n</script>\n\n<style scoped>\n    .btn-travel{\n        background: #e17e4e;\n        background-color: rgb(225, 126, 78);\n        background-position-x: 0%;\n        background-position-y: 0%;\n        background-image: none;\n        background-size: auto;\n        background-image: linear-gradient(to bottom left, #e17e4e, #ffbf00, #e17e4e);\n        background-size: 210% 210%;\n        background-position: top right;\n        background-color: #e17e4e;\n        transition: all 0.15s ease;\n        box-shadow: none;\n        color: #ffffff;\n        max-width: 300px;\n        border: none;\n    }\n</style>\n<style>\n    /*.multiselect-title {*/\n    /*    color: #062348 !important;*/\n    /*    font-family: \"Bello Pro\";*/\n    /*    !*font-weight: 700;*!*/\n    /*}*/\n    /*.multiselect__tags {*/\n    /*    border: 1px solid #f08b23;*/\n    /*    border-radius: 20px;*/\n    /*    font-size: 1rem;*/\n    /*    min-height: 40px;*/\n    /*}*/\n    /*.multiselect__tag {*/\n    /*    background: #062348;*/\n    /*}*/\n    /*.multiselect__tag-icon::after {*/\n    /*    color:#fff;*/\n    /*}*/\n    /*.multiselect__tag-icon:focus, .multiselect__tag-icon:hover {*/\n    /*    background: #f08b23;*/\n    /*}*/\n    /*.multiselect__option--highlight::after {*/\n    /*    background: #f08b23;*/\n    /*}*/\n    /*.multiselect__option--highlight{*/\n    /*    background: #f08b23;*/\n    /*}*/\n    /*.multiselect{*/\n    /*    color: #062348;*/\n    /*    font-family: \"Open Sans\" !important;*/\n    /*    font-weight: 900;*/\n    /*}*/\n    /*.multiselect__placeholder {*/\n    /*    color: #062348;*/\n    /*}*/\n    /*.multiselect__input:focus, .multiselect__single:focus {*/\n    /*    border-color: #062348;*/\n    /*}*/\n    /*.multiselect__input::placeholder {*/\n    /*    color: #062348 !important;*/\n    /*    font-family: Open Sans !important;*/\n    /*    font-weight: 700;*/\n    /*}*/\n    /*.multiselect--disabled {*/\n    /*    background: white;*/\n    /*}*/\n    /*.multiselect--disabled .multiselect__select {*/\n    /*    background: transparent;*/\n    /*}*/\n    /*.multiselect__spinner::after, .multiselect__spinner::before {*/\n    /*    position: absolute;*/\n    /*    content: \"\";*/\n    /*    top: 50%;*/\n    /*    left: 50%;*/\n    /*    margin: -8px 0 0 -8px;*/\n    /*    width: 16px;*/\n    /*    height: 16px;*/\n    /*    border-radius: 100%;*/\n    /*    border: 3px solid transparent;*/\n    /*    border-top-color: transparent;*/\n    /*    border-top-color: #ffa500;*/\n    /*    box-shadow: 0 0 0 1px transparent;*/\n    /*    background: transparent;*/\n    /*}*/\n    /*.multiselect__spinner{*/\n    /*    border-radius: 50px;*/\n    /*}*/\n</style>\n"],"sourceRoot":""}]);
